'use client';

import { useLanguage } from '@/app/context/language-context';

import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Progress } from '@/components/ui/progress';
import { Utensils, Bus, Film, Zap, Heart, GraduationCap, ShoppingBag, MoreHorizontal, Plus } from 'lucide-react';
import { Button } from '@/components/ui/button';

export default function CategoriesPage() {
    const { t } = useLanguage();

    const categories = [
        { id: 'food', name: t('Food'), icon: Utensils, color: 'text-red-500', bg: 'bg-red-100 dark:bg-red-900/20', limit: 5000000, spent: 3200000 },
        { id: 'transportation', name: t('Transportation'), icon: Bus, color: 'text-blue-500', bg: 'bg-blue-100 dark:bg-blue-900/20', limit: 2000000, spent: 850000 },
        { id: 'entertainment', name: t('Entertainment'), icon: Film, color: 'text-purple-500', bg: 'bg-purple-100 dark:bg-purple-900/20', limit: 1500000, spent: 1200000 },
        { id: 'utilities', name: t('Utilities'), icon: Zap, color: 'text-yellow-500', bg: 'bg-yellow-100 dark:bg-yellow-900/20', limit: 3000000, spent: 2100000 },
        { id: 'health', name: t('Health'), icon: Heart, color: 'text-green-500', bg: 'bg-green-100 dark:bg-green-900/20', limit: 1000000, spent: 150000 },
        { id: 'education', name: t('Education'), icon: GraduationCap, color: 'text-indigo-500', bg: 'bg-indigo-100 dark:bg-indigo-900/20', limit: 2000000, spent: 0 },
        { id: 'shopping', name: t('Shopping'), icon: ShoppingBag, color: 'text-pink-500', bg: 'bg-pink-100 dark:bg-pink-900/20', limit: 4000000, spent: 3800000 },
        { id: 'other', name: t('Other'), icon: MoreHorizontal, color: 'text-gray-500', bg: 'bg-gray-100 dark:bg-gray-800', limit: 1000000, spent: 250000 },
    ];

    return (
        <div className="space-y-6">
            <div className="flex items-center justify-between">
                <h1 className="text-3xl font-bold tracking-tight text-gradient">
                    {t('Categories')}
                </h1>
                <Button className="bg-primary text-primary-foreground hover:bg-primary/90">
                    <Plus className="mr-2 h-4 w-4" /> {t('Add')}
                </Button>
            </div>

            <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-4">
                {categories.map((category) => {
                    const percentage = Math.min((category.spent / category.limit) * 100, 100);
                    return (
                        <Card key={category.id} className="border-0 shadow-md hover:shadow-lg transition-shadow">
                            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                                <CardTitle className="text-sm font-medium">
                                    {category.name}
                                </CardTitle>
                                <div className={`p-2 rounded-full ${category.bg}`}>
                                    <category.icon className={`h-4 w-4 ${category.color}`} />
                                </div>
                            </CardHeader>
                            <CardContent>
                                <div className="text-2xl font-bold">
                                    {category.spent.toLocaleString('vi-VN')}đ
                                </div>
                                <p className="text-xs text-muted-foreground mb-3">
                                    / {category.limit.toLocaleString('vi-VN')}đ
                                </p>
                                <Progress value={percentage} className={`h-2 ${percentage > 90 ? 'bg-red-100' : ''}`} />
                            </CardContent>
                        </Card>
                    );
                })}
            </div>
        </div>
    );
}
